import React from 'react';
import { Box, Container, Typography, Paper } from '@mui/material';
import { styled } from '@mui/material/styles';

const StyledPaper = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(4),
  marginTop: theme.spacing(4),
  marginBottom: theme.spacing(4),
}));

const PrivacyPolicy = () => {
  return (
    <Container maxWidth="md">
      <Box sx={{ py: 4 }}>
        <Typography variant="h3" component="h1" gutterBottom align="center" sx={{ mb: 4, fontWeight: 800 }}>
          Poltica de Privacidade
        </Typography>

        <StyledPaper elevation={0}>
          <Typography variant="h5" gutterBottom sx={{ mb: 3, fontWeight: 600 }}>
            1. Introduo
          </Typography>
          <Typography paragraph>
            Esta Poltica de Privacidade descreve como suas informaes pessoais so coletadas, usadas e compartilhadas quando voc visita ou interage com nosso site.
          </Typography>

          <Typography variant="h5" gutterBottom sx={{ mb: 3, mt: 4, fontWeight: 600 }}>
            2. Informaes que Coletamos
          </Typography>
          <Typography paragraph>
            Coletamos apenas informaes essenciais para o funcionamento do site e para responder s suas mensagens de contato:
          </Typography>
          <ul>
            <Typography component="li" paragraph>
              Nome
            </Typography>
            <Typography component="li" paragraph>
              Email
            </Typography>
            <Typography component="li" paragraph>
              Telefone
            </Typography>
            <Typography component="li" paragraph>
              Mensagem (quando voc utiliza o formulrio de contato)
            </Typography>
          </ul>

          <Typography variant="h5" gutterBottom sx={{ mb: 3, mt: 4, fontWeight: 600 }}>
            3. Uso de Cookies
          </Typography>
          <Typography paragraph>
            Utilizamos apenas cookies essenciais para garantir o funcionamento adequado do site. No utilizamos cookies de rastreamento ou analytics.
          </Typography>

          <Typography variant="h5" gutterBottom sx={{ mb: 3, mt: 4, fontWeight: 600 }}>
            4. Como Usamos Suas Informaes
          </Typography>
          <Typography paragraph>
            As informaes coletadas so utilizadas exclusivamente para:
          </Typography>
          <ul>
            <Typography component="li" paragraph>
              Responder s suas mensagens de contato
            </Typography>
            <Typography component="li" paragraph>
              Melhorar a experincia de navegao
            </Typography>
            <Typography component="li" paragraph>
              Garantir a segurana do site
            </Typography>
          </ul>

          <Typography variant="h5" gutterBottom sx={{ mb: 3, mt: 4, fontWeight: 600 }}>
            5. Compartilhamento de Dados
          </Typography>
          <Typography paragraph>
            No compartilhamos suas informaes pessoais com terceiros, exceto quando necessrio para o funcionamento do site ou quando exigido por lei.
          </Typography>

          <Typography variant="h5" gutterBottom sx={{ mb: 3, mt: 4, fontWeight: 600 }}>
            6. Seus Direitos
          </Typography>
          <Typography paragraph>
            Voc tem o direito de:
          </Typography>
          <ul>
            <Typography component="li" paragraph>
              Acessar suas informaes pessoais
            </Typography>
            <Typography component="li" paragraph>
              Solicitar a correo de dados imprecisos
            </Typography>
            <Typography component="li" paragraph>
              Solicitar a excluso de seus dados
            </Typography>
            <Typography component="li" paragraph>
              Retirar seu consentimento a qualquer momento
            </Typography>
          </ul>

          <Typography variant="h5" gutterBottom sx={{ mb: 3, mt: 4, fontWeight: 600 }}>
            7. Contato
          </Typography>
          <Typography paragraph>
            Para questes relacionadas  privacidade, entre em contato atravs do formulrio de contato do site.
          </Typography>

          <Typography variant="h5" gutterBottom sx={{ mb: 3, mt: 4, fontWeight: 600 }}>
            8. Alteraes
          </Typography>
          <Typography paragraph>
            Podemos atualizar esta poltica de privacidade periodicamente. A verso mais recente estar sempre disponvel nesta pgina.
          </Typography>

          <Typography variant="body2" color="text.secondary" sx={{ mt: 4 }}>
            ltima atualizao: {new Date().toLocaleDateString('pt-BR')}
          </Typography>
        </StyledPaper>
      </Box>
    </Container>
  );
};

export default PrivacyPolicy; 