import React from 'react';
import { Box, Typography, Container, Paper } from '@mui/material';
import { styled } from '@mui/material/styles';

const PageContainer = styled(Box)(({ theme }) => ({
  padding: '80px 20px',
  backgroundColor: '#f5f5f5',
  minHeight: '100vh',
}));

const StyledPaper = styled(Paper)(({ theme }) => ({
  padding: '40px',
  marginTop: 0,
  backgroundColor: 'white',
  boxShadow: 'none',
}));

const NoBulletList = styled('ul')({
  listStyle: 'none',
  padding: 0,
  margin: 0,
  textAlign: 'center',
});

const ChildTherapy: React.FC = () => {
  return (
    <PageContainer>
      <Container maxWidth="lg">
        <Typography variant="h3" component="h1" gutterBottom align="center" sx={{ color: '#1B5E20' }}>
          Psicoterapia Infantil
        </Typography>
        <StyledPaper elevation={0}>
          <Typography variant="h5" gutterBottom sx={{ color: '#2E7D32' }}>
            O que  Psicologia Infantil?
          </Typography>
          <Typography paragraph align="center">
            A psicologia infantil  uma rea especializada que trabalha com o desenvolvimento emocional, 
            social e cognitivo das crianas. Atravs de tcnicas ldicas e apropriadas para cada fase do 
            desenvolvimento, ajudamos a criana a expressar suas emoes e lidar com suas dificuldades de 
            forma saudvel.
          </Typography>

          <Typography variant="h5" gutterBottom sx={{ color: '#2E7D32', mt: 4 }}>
            Benefcios da Psicologia Infantil
          </Typography>
          <NoBulletList>
            <li>Avaliao do desenvolvimento infantil</li>
            <li>Terapia ldica e jogos teraputicos</li>
            <li>Orientao parental e familiar</li>
            <li>Desenvolvimento de habilidades sociais</li>
            <li>Acompanhamento escolar</li>
            <li>Interveno precoce em dificuldades de desenvolvimento</li>
            <li>Suporte emocional para a criana</li>
          </NoBulletList>

          <Typography variant="h5" gutterBottom sx={{ color: '#2E7D32', mt: 4 }}>
            Como Funciona
          </Typography>
          <Typography paragraph align="center">
            As sesses so realizadas semanalmente, com durao de 45 minutos. O processo teraputico 
             conduzido atravs de brincadeiras, jogos e atividades ldicas, permitindo que a criana 
            se expresse naturalmente e desenvolva suas habilidades emocionais e sociais.
          </Typography>

          <Typography variant="h5" gutterBottom sx={{ color: '#2E7D32', mt: 4 }}>
            Quando Buscar Ajuda?
          </Typography>
          <Typography paragraph align="center">
            A terapia infantil pode ser benfica quando a criana apresenta:
          </Typography>
          <NoBulletList>
            <li>Dificuldades de comportamento</li>
            <li>Problemas de aprendizagem</li>
            <li>Dificuldades de socializao</li>
            <li>Ansiedade e medos excessivos</li>
            <li>Problemas de sono ou alimentao</li>
            <li>Dificuldades de adaptao</li>
            <li>Traumas ou situaes de luto</li>
          </NoBulletList>
        </StyledPaper>
      </Container>
    </PageContainer>
  );
};

export default ChildTherapy; 