import React from 'react';
import { Box, Typography, Container, Paper } from '@mui/material';
import { styled } from '@mui/material/styles';

const PageContainer = styled(Box)(({ theme }) => ({
  padding: '80px 20px',
  backgroundColor: '#f5f5f5',
  minHeight: '100vh',
}));

const StyledPaper = styled(Paper)(({ theme }) => ({
  padding: '40px',
  marginTop: 0,
  backgroundColor: 'white',
  boxShadow: 'none',
}));

const NoBulletList = styled('ul')({
  listStyle: 'none',
  padding: 0,
  margin: 0,
  textAlign: 'center',
});

const YouthTherapy: React.FC = () => {
  return (
    <PageContainer>
      <Container maxWidth="lg">
        <Typography variant="h3" component="h1" gutterBottom align="center" sx={{ color: '#1B5E20' }}>
          Terapia para Adolescentes
        </Typography>
        <StyledPaper elevation={0}>
          <Typography variant="h5" gutterBottom sx={{ color: '#2E7D32' }}>
            O que  Terapia para Jovens?
          </Typography>
          <Typography paragraph align="center">
            A terapia para jovens  um espao seguro e acolhedor onde adolescentes podem expressar suas 
            preocupaes, medos e anseios. Atravs de uma abordagem adaptada  fase de desenvolvimento, 
            trabalhamos questes especficas da adolescncia, promovendo o desenvolvimento emocional e 
            social saudvel.
          </Typography>

          <Typography variant="h5" gutterBottom sx={{ color: '#2E7D32', mt: 4 }}>
            Benefcios da Terapia para Jovens
          </Typography>
          <NoBulletList>
            <li>Avaliao do desenvolvimento emocional e social</li>
            <li>Orientao vocacional e planejamento de carreira</li>
            <li>Gesto de emoes e desenvolvimento de resilincia</li>
            <li>Melhoria nas habilidades sociais e comunicao</li>
            <li>Apoio familiar e orientao aos pais</li>
            <li>Desenvolvimento de autoestima e autoconfiana</li>
            <li>Estratgias para lidar com presso social e bullying</li>
          </NoBulletList>

          <Typography variant="h5" gutterBottom sx={{ color: '#2E7D32', mt: 4 }}>
            Como Funciona
          </Typography>
          <Typography paragraph align="center">
            As sesses so realizadas semanalmente, com durao de 50 minutos. O processo teraputico 
             adaptado  idade e necessidades do adolescente, utilizando tcnicas e abordagens apropriadas 
            para esta fase do desenvolvimento.
          </Typography>

          <Typography variant="h5" gutterBottom sx={{ color: '#2E7D32', mt: 4 }}>
            Quando Buscar Ajuda?
          </Typography>
          <Typography paragraph align="center">
            A terapia pode ser benfica quando o adolescente apresenta:
          </Typography>
          <NoBulletList>
            <li>Dificuldades de relacionamento com famlia e amigos</li>
            <li>Problemas de autoestima e autoconfiana</li>
            <li>Ansiedade e estresse</li>
            <li>Dificuldades escolares</li>
            <li>Mudanas de humor significativas</li>
            <li>Dificuldades de adaptao</li>
            <li>Questes relacionadas  identidade</li>
          </NoBulletList>
        </StyledPaper>
      </Container>
    </PageContainer>
  );
};

export default YouthTherapy; 